<!--  -->
<template>
    <div class="searchheader-box">
        <h2>Search Github Users</h2>
        <p>
            <input type="text" v-model="username" placeholder="请输入用户名">
            <button class="searchheader-btn" @click="searchUser">Search</button>
        </p>
    </div>
</template>

<script>
import PubSub from 'pubsub-js'
export default {
data () {
    return {
        username:'',
    }
},

props: {},

components: {},

created () {},

computed: {},

mounted (){},

methods: {
    searchUser(){
        const searchName = this.username.trim()
        if(searchName){
            PubSub.publish('searchUser',searchName)
            this.searchName = ''
        }
    }
}
}

</script>
<style>
.searchheader-box{
    padding: 40px 0;
    box-sizing: border-box;
    background-color: lightblue;
}
.searchheader-btn{
    margin-left: 10px;
}
</style>
