<!--  -->
<template>
    <div class="result-box">
        <h3 v-if="firstStatus">请输入用户名...</h3>
        <h3 v-if="loading">LOADING...</h3>
        <h3 v-if="errMsg">{{errMsg}}</h3>

        <div class="result-user-box">
            <div v-for="(item,index) in userList" :key="index" class="result-user-item">
                <a :href="item.url" class="result-user-item-a" target="_blank"> 
                    <img :src="item.avatar_url" alt="">
                </a>
                <p>{{item.name}}</p>
            </div>
        </div>
    </div>
</template>

<script>
import PubSub from 'pubsub-js'
import axios from 'axios'
export default {
data () {
    return {
        firstStatus:true,
        loading:false,
        userList:[],
        errMsg:''
    }
},

props: {},

components: {},

created () {},

computed: {},

mounted (){
    // 点击搜素之后 订阅事件
    PubSub.subscribe('searchUser',(msg,searchName) => {
        const url = `https://api.github.com/search/users?q=${searchName}`
        // 每次订阅都要初始化数据
        this.firstStatus = false
        this.loading = true
        this.userList = null
        this.errMsg = ''

        axios.get(url).then(
            response => {
                const result = response.data
                const users = result.items.map( item => ({
                    url:item.html_url,
                    avatar_url:item.avatar_url,
                    name:item.login
                }))
                this.loading= false
                this.userList = users
            }
        ).catch(error => {
        console.log('获取用户信息失败',error)
        this.errMsg = '请求失败'
        })
    })
},

methods: {}
}

</script>
<style>
.result-box>h3{
    text-align: left;
}
.result-user-box{
    margin: auto;
    width: 1760px;
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
}
.result-user-item{
    margin: 10px;
    width: 200px;
    /* width: 20%; */
    border: 1px solid #000;
    box-sizing: border-box;
    border-radius: 5px;
    box-shadow: 0 0 10px gray;
}
.result-user-item-a,.result-user-item img{
    width: 100%;
    height: 180px;
}
</style>
