<!--  -->
<template>
<div class="basic-box">
    <ul class="basic-ul">
        <li>
            <h3>vue-resource</h3>
            <div>
                <p>
                    <input type="text" v-model="resourceVal" />
                    <button @click="getByResource" class="search-btn">搜索</button>
                </p>

                <p v-if="!RepoList">loading...</p>

                <ol v-else>
                    <p>排名前三的仓库是：</p>
                    <li v-for="(item,index) in RepoList" :key="index">
                        <p>
                            <a :href="item.html_url">fullName:{{item.full_name}}</a>
                        </p>
                        <p>description:{{item.description}}</p>
                    </li>
                </ol>
            </div>
        </li>

        <li>
            <h3>axios</h3>
            <div>
                <p>
                    <input type="text" v-model="axiosVal" />
                    <button @click="getByAxios" class="search-btn">搜索</button>
                </p>
                <p v-if="!RepoList2">loading...</p>
                <ol v-else>
                    <p>排名前三的仓库是：</p>
                    <li v-for="(item,index) in RepoList2" :key="index">
                        <p>
                        <a :href="item.html_url">fullName:{{item.full_name}}</a>
                        </p>
                        <p>description:{{item.description}}</p>
                    </li>
                </ol>
            </div>
        </li>
    </ul>
</div>
</template>

<script>
import axios from "axios";
export default {
data() {
return {
    resourceVal: "",
    axiosVal: "",
    RepoList: [],
    RepoList2: []
};
},
components: {},
methods: {
getByResource() {
    const { resourceVal } = this;
    console.log("resourceVal", resourceVal);
    const resourceUrl = `https://api.github.com/search/repositories?q=${resourceVal}&sort=stars`;
    this.$http.get(resourceUrl).then(
    response => {
        const result = response.data;
        console.log("result", result);
        this.RepoList = (result.items).splice(0,3);
    },
    response => {
        console.log("请求失败", response);
    }
    );
},
getByAxios() {
    const { axiosVal } = this;
    console.log("axiosVal", axiosVal);
    const axiosUrl = `https://api.github.com/search/repositories?q=${axiosVal}&sort=stars`;
    axios
    .get(axiosUrl)
    .then(response => {
        const result = response.data;
        console.log("result", result);
        this.RepoList2 = (result.items).splice(0,3);
    })
    .catch(error => {
        console.log("请求失败2", error);
    });
}
}
};
</script>
<style>
.basic-ul {
    padding: 0;
    margin: 0;
}
.basic-ul > li {
    padding: 0 20px;
    box-sizing: border-box;
    list-style: none;
    margin-bottom: 20px;
    text-align: left;
    border: 1px solid gray;
    border-radius: 5px;
}
.search-btn {
    margin-left: 15px;
}
</style>
